import { Button } from '@popup/shared/components/Button';
import { clsx } from 'clsx';
import { useDebugButton } from '../hooks/useDebugButton';

import styles from './DebugButton.module.css';

export function DebugButton({
  className,
}: {
  className?: string;
}): JSX.Element {
  const { handleCopy, isCopied } = useDebugButton();
  return (
    <>
      <Button
        variant="link"
        className={clsx(styles.link, className)}
        onClick={handleCopy}
      >
        <div className={clsx(styles.info, { [styles.copied]: isCopied })}>
          <span>Отладка</span>
        </div>
        <span
          className={clsx(styles['copy-text'], {
            [styles.copied]: isCopied,
          })}
        >
          Скопировано
        </span>
      </Button>
    </>
  );
}
