import { clsx } from 'clsx';
import { useState } from 'react';
import { setLocalStorageData } from '../../../../app/storage/local/shared';
import { ActionButton } from '../ActionButton';
import { useFeedbackOverlay } from '../../contexts/FeedbackOverlayContext';
import { CloseButton } from './CloseButton/CloseButton';
import { useNegativeFeedbackUrl } from './hooks/useNegativeFeedbackUrl';
import styles from './Feedback.module.css';

async function setFeedbackProvided(): Promise<void> {
  await setLocalStorageData({ feedbackProvided: true });
}

export function Feedback(): JSX.Element {
  const { isFeedbackOverlayShown, closeFeedbackOverlay } = useFeedbackOverlay();
  const { negativeFeedbackUrl } = useNegativeFeedbackUrl();

  const [isPositive, setIsPositive] = useState(false);
  const [isNegative, setIsNegative] = useState(false);

  function handleClickPositiveFeedback(): void {
    setIsPositive(true);
    void setFeedbackProvided();
  }

  function handleClickNegativeFeedback(): void {
    setIsNegative(true);
    void setFeedbackProvided();
  }

  function handleClickReview(isPositive: boolean): void {
    const url = isPositive
      ? process.env.POPUP_HTML_PUBLIC_FEEDBACK_URL
      : negativeFeedbackUrl;
    if (url) {
      chrome.tabs.create({ url });
    }
    closeFeedbackOverlay();
  }



  function renderFeedbackContent(): JSX.Element {
    if (isPositive) {
      return (
        <>
          <CloseButton />
          <h3>Пожалуйста, оцените нас:</h3>

          <ActionButton onClick={() => handleClickReview(true)}>
            ⭐ Поставить оценку
          </ActionButton>
        </>
      );
    }

    if (isNegative) {
      return (
        <>
          <CloseButton />
          <h3>Пожалуйста расскажите, что пошло не так:</h3>
          <ActionButton onClick={() => handleClickReview(false)}>
            🤔 Оставить отзыв
          </ActionButton>
        </>
      );
    }

    return (
      <>
        <CloseButton />
        <h3>Всё работало хорошо?</h3>
        <ActionButton onClick={handleClickPositiveFeedback}>
          👍 Да, все отлично!
        </ActionButton>
        <ActionButton onClick={handleClickNegativeFeedback}>
          👎 Нет, были проблемы.
        </ActionButton>
      </>
    );
  }

  return (
    <div
      className={clsx(styles.overlay, {
        [styles['overlay-visible']]: isFeedbackOverlayShown,
      })}
    >
      <div className={styles['overlay-content']}>{renderFeedbackContent()}</div>
    </div>
  );
}
