import { Link } from 'react-router-dom';

import { useFreePremiumTrial } from '@popup/project/youboost-neo/hooks/useFreePremiumTrial';
import { RoutePath } from '@popup/project/youboost-neo/routes/types/RoutePath';
import { Button } from '@popup/shared/components/Button';
import { usePremiumData } from '@popup/shared/hooks/usePremiumData';

import { Loader } from '../../shared/Loader';
import { PremiumMiniHeader } from '../../shared/PremiumMiniHeader';

import styles from './SmallPremiumPromo.module.css';

export function SmallPremiumPromo(): JSX.Element {
  const { userId } = usePremiumData();
  const { canTryFreePremiumTrial, isCheckingFreePremiumTrial } =
    useFreePremiumTrial();

  if (isCheckingFreePremiumTrial) {
    return <Loader />;
  }

  return (
    <div className={styles.wrapper}>
      <PremiumMiniHeader userId={userId} />
      <h3 className={styles.title}>
        4К-качество, отсутствие рекламы, открывает весь Интернет
      </h3>

      <Link
        to={canTryFreePremiumTrial ? RoutePath.PremiumInfo : RoutePath.Tariffs}
        className={styles.button}
      >
        <Button variant="primary" className={styles.button} size="small">
          {canTryFreePremiumTrial
            ? 'Попробовать бесплатно'
            : 'Подключить Premium'}
        </Button>
      </Link>
    </div>
  );
}
